import axios from 'axios';
import api from './api';
import Store from '../store';
const axio = axios.create({
	// baseURL: process.env.BASE_API, // nodebaseURL
	timeout: 15000, // 
	withCredentials: true // cookie
})
let loading //loading
function startLoading() { //Element loading-start 
	loading = $loading({
		lock: true,
		text: '',
		target: document.getElementById('loadingMain')
	})
}

function endLoading() { //Element loading-close 
	loading.close()
}
// showFullScreenLoading() tryHideFullScreenLoading() 
// needLoadingRequestCountshowFullScreenLoading needLoadingRequestCount + 1
//tryHideFullScreenLoading()needLoadingRequestCount - 1needLoadingRequestCount 0  loading
let needLoadingRequestCount = 0
export function showFullScreenLoading() {
	if (needLoadingRequestCount === 0) {
		startLoading()
	}
	needLoadingRequestCount++
}

export function tryHideFullScreenLoading() {
	if (needLoadingRequestCount <= 0) return
	needLoadingRequestCount--
	if (needLoadingRequestCount === 0) {
		endLoading()
	}
}
// http request  
axio.interceptors.request.use(
	config => {
		if (Store.state.loadingMain) {
			showFullScreenLoading()
		}
		let reqUrl = api.serverDevUrl + config.url
		if (/s1-sandbox.tiejin/.test(window.location.href)) {
			reqUrl = 'https://api-sandbox.tiejin.cn/command/' + config.url;
		} else if (/sadmin-qa.tiejin/.test(window.location.href)) {
			reqUrl = 'https://api-qa.tiejin.cn/command/' + config.url;
		} else if (/admin.tiejin/.test(window.location.href)) {
			reqUrl = api.serverUrl + config.url;
		} else if (/admin-test.tiejin/.test(window.location.href)) {
			reqUrl = api.serverUrl + config.url;
		}
		console.log('reqUrl===', reqUrl)
		config.url = reqUrl;
		if (config.url.indexOf('/command/closer_sys.login') === -1 && config.url.indexOf('/command/closer_sys.register') === -1 && config.url.indexOf('/command/closer_sys.send_code') === -1) {
			try {
				let t = Cookies.get('_tcloser') || Store.state.token;
				config.headers.Authorization = "CloserSysAuth " + t;
			} catch (e) {
				console.log(e)
			}
		}
		return config;
	},
	err => {
		tryHideFullScreenLoading();
		return Promise.reject(err).catch(err);
	});
// http response  
axio.interceptors.response.use(
	response => {
		tryHideFullScreenLoading();
		return response;
	},
	(err) => {
		if (err && err.response) {
			switch (err.response.status) {
				case 400:
					err.message = ''
					break

				case 401:
					err.message = ''
					router.push({
						name: 'worldcupIndex'
					})
					break

				case 403:
					err.message = ''
					router.push({
						name: 'worldcupIndex'
					})
					break

				case 404:
					err.message = `: ${err.response.config.url}`
					break

				case 408:
					err.message = ''
					break

				case 500:
					err.message = ''
					break

				case 501:
					err.message = ''
					break

				case 502:
					err.message = ''
					break

				case 503:
					err.message = ''
					break

				case 504:
					err.message = ''
					break

				case 505:
					err.message = 'HTTP'
					break

				default:
			}
		} else {
			tryHideFullScreenLoading();
			err.message = ''
		}
		if (err && err.response && err.response.data && err.response.data.message) {
			console.warn(err.response.data.message)
		} else {
			console.warn(err.message)
		}
		tryHideFullScreenLoading();
		return Promise.reject(err).catch(err)
	});
export default axio